        startLogin: function () {
            //登录
            if (this.isRotate) {
                //判断是否加载中，避免重复点击请求
                return false;
            }
            if (this.mynote.phone.length == "") {
                uni.showToast({
                    icon: 'none',
                    position: 'bottom',
                    title: '用户名/手机号码不能为空'
                });
                return;
            }
            if (this.mynote.password.length < 5) {
                uni.showToast({
                    icon: 'none',
                    position: 'bottom',
                    title: '请输入密码不少于6位数'
                });
                return;
