                    password: this.mynote.password
                });
                setTimeout(function () {
                    _this.isRotate = false
                    uni.switchTab({
                        url: '../mine/index'
                    });
                }, 3000)
            } else {
                setTimeout(() => {
                    _this.isRotate = false
                    uni.showToast({
