        },

        /**
         * //检测用户是否授权
         * @param  {type} e {description}
         * @return {type} {description}
         */
        GetUserInfo: function (e) {
            // console.log(e);
            let that = this;
            // 获取用户信息
            wx.getSetting({
                success (res) {
                    // console.log("res", res)
                    if (res.authSetting['scope.userInfo']) {
                        console.log("已授权=====")
                        // 已经授权，可以直接调用 getUserInfo 获取头像昵称
                        wx.getUserInfo({
                            success (res) {
                                console.log("获取用户信息成功", res)
                                that.handleRegistered(that.loginCode)
                            },
                            fail (res) {
                                console.log("获取用户信息失败", res)
                            }
                        })
                    } else {
                        console.log("未授权=====")
                        // that.showSettingToast("请授权")
                    }
                }
            })
        },
        // 微信小程序注册
        handleRegistered: function (JSCODE) {
            loginRegistered(JSCODE).then(res => {
                if (res[1].data.code == 0) {
                    this.sessionKey = JSON.parse(res[1].data.data).session_key
                    this.openid = JSON.parse(res[1].data.data).openid
                }
            }).catch(err => {
                uni.showModal({
                    content: err.errMsg,
                    showCancel: false
                });
            }).finally(() => { })
        },
        //在授权失败时，我们调用that.showSettingToast()方法
        showSettingToast: function (e) {
            wx.showModal({
                title: '提示！',
                confirmText: '去设置',
                showCancel: false,
                content: e,
                success: function (res) {
                    if (res.confirm) {
                        wx.navigateTo({
                            url: '../setting/setting',
                        })
                    }
                }
            })
        },

        onGetPhoneNumber: function (e) {
            console.log("onGetPhoneNumber", e);
            console.log(e.detail.errMsg);
            console.log(e.detail.iv);
            console.log(e.detail.encryptedData);
            uni.showModal({
                title: "onGetPhoneNumber",
                content: e.detail.errMsg
            })
